import API from '../../default';
import {getLoginSalt} from '../../hash';
import * as types from '../types';


export const ViewAll = () => {
  const hash = getLoginSalt('','');
  return dispatch => {
    dispatch({
      type: types.VIEW_ALL_CARS,
      payload: API({
        method: 'post',
        url: `/?end=viewallcars`,
        data: {
          "page": 1,
          "hashpass": hash,
          "numberofitem": 5
        }
      })
    })
  }
}

















