import API from '../../default';
import {getLoginSalt} from '../../hash';
import * as types from '../types';


export const SearchCars = (data) => {
  const hash = getLoginSalt('','');
  const {location, highest, lowest, model, type} = data
  return dispatch => {
    dispatch({
      type: types.SEARCH_CARS,
      payload: API({
        method: 'post',
        url: `/?end=searchcarsparameter`,
        data: {
          "make":"jcj ",
          "model": "cvsr",
          "series":"S-class",
          "year":"mcmc ",
          "milleage":" mmc",
          "bodytype":" mcmc",
          "transtype":" mxxm",
          "condition":" mmxm",
          "flexibility":"matchall",
          "page":"1",
          "numberofitem":"20",
          "hashpass": hash,
        }
      })
    })
  }
}