import React, { Component } from 'react';
import {connect} from 'react-redux';
import {Link} from 'react-router-dom';

import { Container,Row,Box,Image, Col, Column, Button} from '../../styles/init';
import Navigation from '../../reuseables/navigation';
import Form from '../../reuseables/form';
import Footer from '../../reuseables/footer';
import {ViewAll} from '../../redux/actions/viewAllCars';
import {SearchCars} from '../../redux/actions/searchCars';
import Spinner from '../../reuseables/spinner';

class CarsList extends Component {

    constructor(props) {        
        super(props)
        this.state = {
            isSearch: this.props.location.search === "" ? false : true,
            searchData: {
                location: "",
                lowest: "",
                highest: "",
                model: "",
                type: ""
            }      
        }
    }
    componentDidMount() {
        if(this.state.isSearch) {
            this.props.searchCars(this.getSearchParams)
        } else { 
            this.props.viewAllCars()
        } 
    }

    componentDidUpdate(prevprops) {
        if(this.props.location.search !== prevprops.location.search){
            this.setState({
                isSearch: false
            })
        }
    }

    getSearchParams = () => {
        const query = new URLSearchParams(this.props.location.search)
        const searchData = []
        for(let param of query.entries()) {
            searchData.push(param[1])
        }
       return searchData
    }

    getLocationData = (value,valid) => { 
        this.setState({
                ...this.state,
                searchData: {
                  ...this.state.searchData,
                  location : value
                }
        })
    }
  
    getHighestData = (value,valid) => { 
        this.setState({
                ...this.state,
                searchData: {
                  ...this.state.searchData,
                  highest : value
                }
        })
    }
  
    getModelData = (value,valid) => { 
        this.setState({
                ...this.state,
                searchData: {
                  ...this.state.searchData,
                  model : value
                }
        })
    }
  
    getLowestData = (value,valid) => { 
        this.setState({
                ...this.state,
                searchData: {
                  ...this.state.searchData,
                  lowest : value
                }
        })
    }
  
    getTypeData = (value,valid) => { 
        this.setState({
                ...this.state,
                searchData: {
                  ...this.state.searchData,
                  type : value
                }
        })
    }

    search = () => {
        this.setState({isSearch: true});
        this.props.searchCars(this.state.searchData); 
    }
    
  render() {
    const {allCarsPayload, searchCarsPayload, searchCarsSuccess, allCarsSuccess} = this.props;
    const success = this.state.isSearch? searchCarsSuccess : allCarsSuccess;
    const payload = this.state.isSearch? searchCarsPayload : allCarsPayload

    let cars =  <Row justify="center">
                    <Spinner />
                </Row>;
               
   if(success) {
        cars = payload.Message?<Row justify="center"> <h1>{payload.Message}</h1> </Row>
             :
              Object.keys(allCarsPayload.cars).map((car)=>{
                    return (
                                <Box width="23%" height="321px" padding="13px" left="1%" right="1%" key={car}>
                                    <Link to={`/cars/${car}`} className="navitem">
                                        <Column height="100%">                                
                                            <Row bottomspacing="34px" height="151px">
                                                <Image src={require('../../assets/car.png')} alt="car"/> 
                                            </Row>
                                            <Row bottomspacing="13px">
                                                <p>{allCarsPayload.cars[car].make + " " + allCarsPayload.cars[car].series}</p>
                                            </Row>
                                            <Row>
                                                <h1 className="linkblue">&euro; {allCarsPayload.cars[car].price}</h1>
                                            </Row>
                                        </Column>
                                    </Link>
                                </Box>
                    )
                }) 
   }

    return (
        <Container>
            <Navigation/>
            <Row bottomspacing="28px" justify="space-between" padding="0px 87px">
                <Form
                    width="20%"
                    placeholder="Select a Model"
                    label="Model"
                    type="select"
                    options={["Toyota"]}
                    data={this.getModelData}
                />
                <Form
                    width="10%"
                    placeholder="Lowest"
                    label="Price Range"
                    type="select"
                    options={["500"]}
                    data={this.getLowestData}
                />
                <Form
                    width="10%"
                    placeholder="Highest"
                    label=""
                    type="select"
                    options={["10000"]}
                    data={this.getHighestData}
                />
                <Form
                    width="20%"
                    placeholder="Select a Location"
                    label="Location"
                    type="select"
                    options={["New York"]}
                    data={this.getLocationData}
                />
                <Form
                    width="15%"
                    placeholder="Select a Type"
                    label="Type"
                    type="select"
                    options={["SUV"]}
                    data={this.getTypeData}
                />
                <Button width="10%" height="58px" style={{alignSelf:"flex-end"}} onClick={this.search}> Search </Button>
            </Row>
            <Row bottomspacing="28px" justify="flex-start" padding="0px 87px" height="400px">
                {cars}
            </Row>
            <Row padding="0 82px" justify="space-between" bottomspacing="80px">
                <Col width="15%" padding="15px" className="bg-grey"></Col>
                <Col width="30%" padding="15px" className="bg-grey"></Col>
                <Col width="15%" padding="15px" className="bg-grey"></Col>
            </Row>
            <Footer />
        </Container>
    )
  }
}
const mapStateToProps = state => {
    return {
        allCarsPayload: state.Viewallcars.cars_payload,
        allCarsLoading: state.Viewallcars.get_cars_loading,
        allCarsSuccess: state.Viewallcars.get_cars_successful,
        searchCarsPayload: state.Searchcars.search_payload,
        searchCarsLoading: state.Searchcars.loading,
        searchCarsSuccess: state.Searchcars.successful,
    }
}

const mapDispatchToProps = dispatch => {
    return {
        viewAllCars: () => dispatch(ViewAll()),
        searchCars: (params) => dispatch(SearchCars(params))
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(CarsList);
